<template>
  <div class="content_box">
    <div class="content_tree">
      <p class="tree_title">分组列表</p>
      <el-input
        placeholder="输入关键字进行过滤"
        v-model="filterText">
      </el-input>
      <el-tree
        class="filter-tree"
        :data="data"
        :props="defaultProps"
        default-expand-all
        :highlight-current="true"
        :filter-node-method="filterNode"
        ref="tree">
      </el-tree>
    </div>
    <div class="content_table">
      <el-row>
        <el-col :span="24">
          <app-table-utils :utils="utils">
            <el-input v-model="queryData.deviceName" slot="query-1"/>
            <el-input v-model="queryData.deviceType" slot="query-2"/>
            <el-button slot="query" class="queryBtn" >查询</el-button>
            <el-button slot="add" class="queryBtn" @click="showDialog({type:'add'})">添加设备</el-button>
          </app-table-utils>
          <div class="table-line"></div>
          <app-tables :table="table"  @pageChange="handleCurrentChange">
            <template slot="column" slot-scope="{data}">
              <template v-if="data.col.key === 'operation'">
                <el-button type="text" @click="showDialog({type:'update', data:data.row})">修改</el-button>
                <el-button type="text" @click="handleDelete(data.row)">删除</el-button>
              </template>
               <template v-else-if="data.col.key === 'state'">
                <p v-if="data.row[data.col.key] == 1"><i class="normal_color"></i><span>正常</span></p>
                <p v-else-if="data.row[data.col.key] == 2"><i class="abnormal_color"></i><span>异常</span></p>
                <p v-else-if="data.row[data.col.key] == 3"><i class="offline_color"></i><span>离线</span></p>
              </template>
              <template v-else-if="data.col.key === 'switch'">
                <el-switch v-model="data.row[data.col.key]"></el-switch>
              </template>
               <template v-else-if="data.col.key === 'deviceName'">
                <span class="deviveName_title_color" @click="showDialog({type:'switch_table', data:data.row})">{{ data.row[data.col.key] || '-' }}</span>
              </template>
              <template v-else>{{data.row[data.col.key] || '-'}}</template>
            </template>
          </app-tables>
        </el-col>
      </el-row>

    </div>
    <device-form ref="DeviceForm" @refresh="init()" />
    <switch-dialog ref="SwitchDialog" @refresh="init()" />
  </div>
</template>
<script>
import DeviceForm from './form/DeviceForm';
import SwitchDialog from './form/SwitchDialog';
export default {
  name:'DeviceMange',
  components: {
    DeviceForm,
    SwitchDialog
  },
  watch: {
    filterText(val) {
      this.$refs.tree.filter(val);
    }
  },
  data(){
    return {
        filterText:null,//树形结构搜索
        defaultProps: {
          children: 'children',
          label: 'label'
        },
        data: [{
          id: 1,
          label: '西藏自治区旅游景区',
          children: [{
            id: 4,
            label: '拉萨旅游景区',
            children: [{
              id: 9,
              label: '扎木措'
            }]
          }]
        }, {
          id: 2,
          label: '四川省旅游景区',
          children: [{
            id: 5,
            label: '成都'
          }, {
            id: 6,
            label: '阆中'
          }]
        }],
        utils: { //表格工具栏
              left: [
                  { label: '设备名称', slot: 'query-1' },
                  { label: '设备型号', slot: 'query-2' },
                  { slot: 'query' },
                  { slot: 'add' },
              ],
              cols: [24]
          },
        queryData:{
          pageNum:1,
          pageSize:10,
          deviceName:null,//设备名称
          deviceType: null,//设备型号
        },
        table: {
              data: [], //表格数据
              total: 0, //总页数
              tableType: 1, //表格类型
              cols: [ //表格列配置
                  {
                      key: '序号',
                      label: '序号'
                  },
                  {
                      key: 'deviceName',
                      label: '设备名称'
                  },
                  {
                      key: '设备型号',
                      label: '设备型号'
                  },
                  {
                      key: '设备ID',
                      label: '设备ID'
                  },
                   {
                      key: '所属分组',
                      label: '所属分组'
                  },
                   {
                      key: '实际位置',
                      label: '实际位置'
                  },
                   {
                      key: '创建时间',
                      label: '创建时间'
                  },
                   {
                      key: 'state',
                      label: '设备状态'
                  },
                   {
                      key: '备注',
                      label: '备注'
                  },
                   {
                      key: 'switch',
                      label: '总开关'
                  },
                   {
                      key: 'operation',
                      label: '操作'
                  }
              ]
          }
    }
  },
  created(){
    this.init();
  },
  methods:{
        /**
     * 初始化数据
     */
    init(){
      this.table.data = [
        {
          '序号':1,
          deviceName: '设备一',
          '设备ID': 'V102',
          '设备型号': 'VSIN01',
          '实际位置': '四川环球广场',
          '所属分组': '四川',
          '备注': '测试专用',
          '创建时间': '2019-09-26 09:30:30',
          'state': 1,
          '备注':'测试',
          switch: true
        },
        {
          '序号':2,
          deviceName: '设备二',
          '设备ID': 'V102',
          '设备型号': 'VSIN01',
          '实际位置': '四川环球广场',
          '所属分组': '四川',
          '备注': '测试专用',
          '创建时间': '2019-09-26 09:30:30',
          'state': 2,
          '备注':'测试',
          switch: false
        },
        {
          '序号':3,
          deviceName: '设备三',
          '设备ID': 'V102',
          '设备型号': 'VSIN01',
          '实际位置': '四川环球广场',
          '所属分组': '四川',
          '备注': '测试专用',
          '创建时间': '2019-09-26 09:30:30',
          'state': 3,
          '备注':'测试',
          switch: true
        }
      ]
    },

     /**
       * 分页器回调
       */
      handleCurrentChange(page) {
          this.queryData.pageNum = page;
          // this.init();
      },

       filterNode(value, data) {
        if (!value) return true;
        return data.label.indexOf(value) !== -1;
      },

         /**
       * 删除
       */
      handleDelete({ id  }) {
          this.$confirm(`此操作将会删除此条数据,是否继续?`, '提示', {
              cancelButtonText: '取消',
              confirmButtonText: '确定',
              type: 'warning',
              center: true,
              customClass: 'bg-warning'
          }).then(async () => {
              // const res = await deleteOneMsg({ id  });
              // if(res.code == '1'){
              //     this.$message.success('删除成功');
              //     this.init();
              //   }else{
              //     // 失败
              //     this.$message.error('删除失败');
              //   }
          }).catch(() => {});
      },


     /**
	     * 弹出层控制
	     */
        showDialog ({ type, id, data }) {
            if (type === 'add') {
              this.$refs.DeviceForm.init({ type, title: '添加设备' });
            } else if (type === 'update') {
              this.$refs.DeviceForm.init({ type, id, data, title: '修改设备' });
            } else if(type === 'switch_table') {
              this.$refs.SwitchDialog.init({ type, id, data, title: '开关设置' });
            }
        },


  }
}
</script>
<style lang="less" scoped>
.content_box{
  padding: 0;
  margin: 0;
  background: rgba(247, 248, 250, 1);
  height: 100%;
  box-shadow:0 0 0 0;
  display: flex;
  flex-flow:row;
}
.content_tree{
  display: inline-block;
  box-sizing: border-box;
  width:180px;
  min-height: calc(92.5vh);
  background:rgba(255,255,255,1);
  border-right: 1px solid rgba(229,229,229,1);
  border-top: 0;
  // padding: 0 20px 0 20px;
  /deep/ .el-input{
    margin: 0 20px 0 20px;
    width: 140px;
  }
  /deep/ .el-tree-node__content{
    min-height: 40px;
  }
}
.tree_title{
  height: 50px;
  line-height: 50px;
  font-size:16px;
  font-family: 'PingFang-SC-Medium';
  font-weight:bold;
  color:rgba(51,51,51,1);
  padding: 0 0 0 20px;
}
.el-tree{
  margin: 20px 0 0 0;
  /deep/ .is-current.is-focusable.treeitem{
    background:rgba(54,123,246,0.13);
      font-size:13px;
      font-family: 'PingFang-SC-Medium';
      font-weight:500;
      color:rgba(54,123,246,1);

  }
  /deep/ .is-current.is-focusable > .el-tree-node__content {

         background:rgba(54,123,246,0.13);
      font-size:13px;
      font-family: 'PingFang-SC-Medium';
      font-weight:500;
      color:rgba(54,123,246,1);

  }
}

.content_table{
  // display: inline-block;
  flex: 1;
  margin:20px 20px 20px 20px;
  // width: 84%;
  background:rgba(255,255,255,1);
  box-shadow:0px 1px 4px 0px rgba(147,150,152,0.1);
  border-radius:2px;
  // margin: 0 0 0 20px;
}

  .normal_color{
    display: inline-block;
    width:8px;
    height:8px;
    background:rgba(34,199,32,1);
    border-radius:50%;
    margin: 0 4px 0 0;
  }
  .abnormal_color{
    display: inline-block;
    width:8px;
    height:8px;
    background:rgba(254,82,82,1);
    border-radius:50%;
    margin: 0 4px 0 0;
  }
  .offline_color{
    display: inline-block;
    width:8px;
    height:8px;
    background:rgba(171,171,171,1);
    border-radius:50%;
    margin: 0 4px 0 0;
  }

  .el-switch{
    width:38px;
    height:20px;
  }
  .el-switch.is-checked /deep/ .el-switch__core {
    border-color: rgba(54,123,246,1) ;
    background:rgba(54,123,246,1) ;
  }

  .deviveName_title_color{
    display: inline-block;
    color: rgba(54,123,246,1) ;
    cursor: pointer;
  }
  .deviveName_title_color:hover{
    text-decoration:underline;
  }

</style>





