<template>
    <dialog-container :dialog-conf="dialogConf">
      <app-tables :table="table"  @pageChange="handleCurrentChange">
        <template slot="column" slot-scope="{data}">
          <template v-if="data.col.key === 'switch'">
              <el-switch v-model="data.row[data.col.key]"></el-switch>
          </template>
          <template v-else>{{data.row[data.col.key] || '-'}}</template>
        </template>
      </app-tables>
    </dialog-container>
</template>
<script type="text/ecmascript-6">
import util from '@/common/method/util';
// import { addRoles, modifyRoles } from '@/http';

export default {
    name: 'SwitchDialog',
    data () {
        return {
            dialogConf: {
                width: '700px',
                isShow: false,
                center: true,
                title: null
            },
            type: null,
            sendData: {},
            id: null,
            table: {
              data: [], //表格数据
              total: 0, //总页数
              tableType: 1, //表格类型
              cols: [ //表格列配置
                  {
                      key: '编号',
                      label: '编号'
                  },
                   {
                      key: 'switchName',
                      label: '开关名称'
                  },
                   {
                      key: 'switch',
                      label: '操作'
                  }
              ]
          }
        };
    },
    methods: {

        /**
		 * 初始化
		 */
        async init ({ type, title, id, data }) {
            this.dialogConf.isShow = true;
            this.id = id;
            this.type = type;
            if (title) this.dialogConf.title = title;
            this.table.data = [
              {
                '编号':1,
                switchName:'集控开关',
                switch:true,
              },
               {
                '编号':2,
                switchName:'集控开关-02',
                switch:false,
              }
            ]
        },

        /**
		 * 提交表单
		 */
        submitForm (formName) {
            this.$refs[formName].validate(async (valid) => {
                if (valid) {
                    // let code = null;
                    // if (this.type === 'update') { //修改
                    //     const ress = await modifyRoles(this.sendData);
                    //     code = ress.code;
                    // } else { //新增
                    //     this.sendData.pid = this.id;
                    //     const res = await addRoles(this.sendData);
                    //     code = res.code;
                    // }
                    // if (code == 200) {
                    //     this.dialogConf.isShow = false;
                    //     this.$message.success(this.$t('msg.operation_success'));
                    //     this.resetForm(formName);
                    //     this.$emit('refresh');
                    // };
                } else {
                    this.$message.error('请填写完表单');
                }
            });
        },

    /**
		 * 重置表单
		 * @param formName
		 */
      resetForm (formName) {
        this.$refs[formName].resetFields();
        this.dialogConf.isShow = false;
      },

           /**
       * 分页器回调
       */
      handleCurrentChange(page) {
          // this.queryData.pageNum = page;
          // this.init();
      },

    }
};
</script>
<style lang="less" scoped>
.plat-form {
    margin: 15px 0 0 0;
    .avatar-uploader-icon:hover {
        border-color: #409eff;
    }
    .avatar-uploader-icon {
        border: 1px dashed #d9d9d9;
        font-size: 28px;
        color: #8c939d;
        width: 178px;
        height: 178px;
        line-height: 178px;
        text-align: center;
    }
    .avatar {
        width: 178px;
        height: 178px;
        display: block;
    }
    .app-submit-btns {
        margin-top: 20px;
        text-align: center;
        /deep/ .el-form-item__content{
          margin-left: 20px !important;
        }
    }

}
  .el-switch{
    width:38px;
    height:20px;
  }
  .el-switch.is-checked /deep/ .el-switch__core {
    border-color: rgba(54,123,246,1) ;
    background:rgba(54,123,246,1) ;
  }
</style>



