<template>
    <dialog-container :dialog-conf="dialogConf">
        <el-row class="app-form plat-form" :gutter="$store.state.config.dialogFormGutterWidth">
            <el-form ref="rhForm" :model="sendData" :label-position="$store.state.config.labelPosition" :rules="rules" label-width="125px" @submit.native.prevent>
                <el-col :span="16" :offset="3">
                   <el-form-item label="归属分组" prop="group">
                        <el-input v-model="sendData.group" disabled/>
                    </el-form-item>
                    <el-form-item label="设备名称" prop="deviceName">
                        <el-input v-model="sendData.deviceName" />
                    </el-form-item>
                    <el-form-item label="设备ID" prop="deviceId">
                        <el-input v-model="sendData.deviceId" />
                    </el-form-item>
                    <el-form-item label="设备型号" prop="deviceType">
                        <el-input v-model="sendData.deviceType" disabled/>
                    </el-form-item>
                    <el-form-item label="设备路数" prop="deviceNumber">
                        <!-- <el-input v-model="sendData.deviceNumber" disabled/> -->
                        <el-select v-model="sendData.deviceNumber">
                          <el-option label="4路" :value="4" />
                          <el-option label="8路" :value="8" />
                          <el-option label="16路" :value="16" />
                        </el-select>
                    </el-form-item>
                    <el-form-item label="实际位置" prop="address">
                        <el-input v-model="sendData.address" />
                    </el-form-item>
                    <el-form-item label="备注" prop="remark">
                        <el-input v-model="sendData.remark" type="textarea" :rows="6" />
                    </el-form-item>
                </el-col>
                <el-col :span="24">
                    <div class="app-submit-btns">
                        <el-form-item class="app-text-center">
                            <el-button @click="submitForm('rhForm')">
                                保存
                            </el-button>
                            <el-button class="resetForm" @click="resetForm('rhForm')">
                                返回
                            </el-button>
                        </el-form-item>
                    </div>
                </el-col>
            </el-form>
        </el-row>
    </dialog-container>
</template>
<script type="text/ecmascript-6">
import util from '@/common/method/util';
// import { addRoles, modifyRoles } from '@/http';

export default {
    name: 'DeviceForm',
    data () {
        return {
            dialogConf: {
                width: '700px',
                isShow: false,
                center: true,
                title: null
            },
            type: null,
            sendData: {},
            initData: {
              group:'下拉选择',//归属分组
              deviceName:null,//设备名称
              deviceId:null,//设备id
              deviceType:'下拉选择',//设备型号
              address:null,//实际位置
              remark:null,//备注
              deviceNumber:null,//设备路数
            },

            rules: {
               group: [
                  { required: true, message: '请选择归属分组', trigger: 'change' },
                ],
                deviceName: [
                  { required: true, message: '请输入设备名称', trigger: 'blur' },
                ],
                deviceId: [
                  { required: true, message: '请输入设备ID', trigger: 'blur' },
                ],
                deviceType: [
                  { required: true, message: '请选择设备型号', trigger: 'change' },
                ],
                deviceNumber: [
                  { required: true, message: '请选择设备型号', trigger: 'change' },
                ],
                address: [
                  { required: true, message: '请输入实际位置', trigger: 'blur' },
                ],
            },
            id: null
        };
    },
    methods: {

        /**
		 * 初始化
		 */
        async init ({ type, title, id, data }) {
            this.dialogConf.isShow = true;
            this.id = id;
            this.type = type;
            if (title) this.dialogConf.title = title;
            if (type === 'add') {
                this.sendData = util.deepCloneObject(this.initData);
            } else if (type === 'update') {
                this.sendData = { ...util.deepCloneObject(this.initData), ...data };
            }
        },

        /**
		 * 提交表单
		 */
        submitForm (formName) {
            this.$refs[formName].validate(async (valid) => {
                if (valid) {
                    // let code = null;
                    // if (this.type === 'update') { //修改
                    //     const ress = await modifyRoles(this.sendData);
                    //     code = ress.code;
                    // } else { //新增
                    //     this.sendData.pid = this.id;
                    //     const res = await addRoles(this.sendData);
                    //     code = res.code;
                    // }
                    // if (code == 200) {
                    //     this.dialogConf.isShow = false;
                    //     this.$message.success(this.$t('msg.operation_success'));
                    //     this.resetForm(formName);
                    //     this.$emit('refresh');
                    // };
                } else {
                    this.$message.error('请填写完表单');
                }
            });
        },

    /**
		 * 重置表单
		 * @param formName
		 */
      resetForm (formName) {
        this.$refs[formName].resetFields();
        this.dialogConf.isShow = false;
      }
    }
};
</script>
<style lang="less" scoped>
.plat-form {
    margin: 15px 0 0 0;
    .avatar-uploader-icon:hover {
        border-color: #409eff;
    }
    .avatar-uploader-icon {
        border: 1px dashed #d9d9d9;
        font-size: 28px;
        color: #8c939d;
        width: 178px;
        height: 178px;
        line-height: 178px;
        text-align: center;
    }
    .avatar {
        width: 178px;
        height: 178px;
        display: block;
    }
    .app-submit-btns {
        margin-top: 20px;
        text-align: center;
        /deep/ .el-form-item__content{
          margin-left: 20px !important;
        }
    }

    .el-select{
      display: block;
    }
}
</style>



